import React, { useState } from 'react';

// Utility function to merge CSS classes (replacement for cn function)
function cn(...classes) {
  return classes.filter(Boolean).join(' ');
}

// Team members data
const teamMembers = [
  // Backend members
  { name: 'Do Duc Anh', jobTitle: 'Backend Developer', image: { name: 'ducanhdo', folder: 'teammates' } },
  { name: 'La Huy Hoang', jobTitle: 'Backend Developer', image: { name: 'hhoang', folder: 'teammates' } },
  { name: 'Luu Trong Dung', jobTitle: 'AI Developer', image: { name: 'trongdung', folder: 'teammates' } },
  
  // Frontend members
  { name: 'Lam Quoc Hung', jobTitle: 'Frontend Developer', image: { name: 'hwan', folder: 'teammates' } },
  { name: 'Thai Bao Nhan', jobTitle: 'Frontend Developer', image: { name: 'nhancat', folder: 'teammates' } }
];

// OptimizeImage Component
const OptimizeImage = ({ src, alt, folder, className }) => {
  const [isLoaded, setIsLoaded] = useState(false);

  const renderContent = () => {
    if (!src) {
      return (
        <div className="absolute inset-0 flex flex-col items-center justify-center gap-3 bg-slate-100/80">
          <svg 
            className="text-4xl text-slate-400 w-8 h-8" 
            fill="currentColor" 
            viewBox="0 0 24 24"
          >
            <path d="M21 19V5c0-1.1-.9-2-2-2H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2zM8.5 13.5l2.5 3.01L14.5 12l4.5 6H5l3.5-4.5z"/>
          </svg>
          <span className="text-slate-600 text-sm">No image available</span>
        </div>
      );
    }

    const imageKitURL = src.startsWith('https') 
      ? src 
      : `https://ik.imagekit.io/3dteacher/${folder}/${src}.jpg?tr=w-auto,h-auto,fo-auto`;

    return (
      <>
        {!isLoaded && <div className="absolute inset-0 bg-slate-200 animate-pulse" />}
        <img 
          src={imageKitURL}
          alt={alt}
          loading="lazy"
          decoding="async"
          onLoad={() => setIsLoaded(true)}
          className={cn(
            'transition-opacity duration-300',
            isLoaded ? 'opacity-100' : 'opacity-0',
            className
          )}
        />
      </>
    );
  };

  return (
    <div className="relative size-full w-full h-full">
      {renderContent()}
    </div>
  );
};

// TeamMember Component
const TeamMember = ({ name, jobTitle, image }) => {
  return (
    <div className="flex flex-col items-center">
      <div className="w-72 h-72 rounded-full shrink-0 grow-0 shadow-xl overflow-hidden">
        <OptimizeImage 
          src={image.name} 
          alt={name} 
          folder={image.folder}
          className="w-full h-full object-cover"
        />
      </div>
      <div className="mt-4 text-center">
        <h3 className="text-2xl font-semibold">{name}</h3>
        <p className="text-xl font-normal text-gray-600">{jobTitle}</p>
      </div>
    </div>
  );
};

// TeamImage Component
const TeamImage = () => {
  const backendMembers = teamMembers.filter(member => 
    member.jobTitle.includes('Backend') || member.jobTitle.includes('AI')
  );

  const frontendMembers = teamMembers.filter(member =>
    member.jobTitle.includes('Frontend')
  );

  return (
    <div className="w-full grid grid-cols-1 md:grid-cols-3 gap-8">
      {backendMembers.map((member, index) => (
        <TeamMember key={index} {...member} />
      ))}
      <div className="col-span-1 md:col-span-3 flex flex-col md:flex-row justify-center gap-10">
        {frontendMembers.map((member, index) => (
          <TeamMember key={index} {...member} />
        ))}
      </div>
    </div>
  );
};

// Main About Us Component
const AboutUs = () => {
  return (
    <section className="w-full min-h-screen flex flex-col items-center pt-16 pb-16 relative">
      {/* Background Pattern */}
      <div
        className={cn(
          'absolute inset-0',
          'bg-[size:40px_40px]',
          'bg-[linear-gradient(to_right,#e4e4e7_1px,transparent_1px),linear-gradient(to_bottom,#e4e4e7_1px,transparent_1px)]',
          'dark:bg-[linear-gradient(to_right,#262626_1px,transparent_1px),linear-gradient(to_bottom,#262626_1px,transparent_1px)]',
          'pointer-events-none'
        )}
      />
      
      {/* Gradient Overlay */}
      <div className="pointer-events-none absolute inset-0 flex items-center justify-center bg-white [mask-image:radial-gradient(ellipse_at_center,transparent_20%,black)] dark:bg-black" />

      {/* Content */}
      <div className="w-full max-w-6xl px-4 flex flex-col items-center text-center gap-8 relative z-10">
        <h1 className="text-4xl md:text-6xl lg:text-8xl font-bold leading-tight">
          Meet our team
        </h1>
        <p className="w-full max-w-2xl text-lg md:text-xl lg:text-2xl font-normal mb-8 text-gray-700">
          Discover our mission and values that drive our commitment to delivering innovative learning experiences.
        </p>
        <div className="w-full max-w-5xl">
          <TeamImage />
        </div>
      </div>
    </section>
  );
};

export default AboutUs;
